import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//RXJS
import { BehaviorSubject } from 'rxjs';
import { first } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Device
import { ApiUserGroup } from '../../../../partials/layout/tacgui/_forms/api-user-group-form/api-user-group.model'
//Service//
import { UserGroupService } from '../user-group.service'

@Component({
  selector: 'kt-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(false)

  formParameters = {
    action: 'add',
    data: new ApiUserGroup,
    old_data: new ApiUserGroup,
    rights: []
  }

  constructor(
    private toastr:ToastrService,
    private ug_service: UserGroupService,
    private router:Router,
    private route: ActivatedRoute
  ) {

  }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.fillForm(+params.get('id'))
    });
  }

  fillForm(id:number){
    console.log(id)
    this.ug_service.get(id).subscribe( data => {
      console.log(data)
      if (!data){
        this.toastr.error('Server Error!')
        this.router.navigate(['../../'], { relativeTo: this.route });
        return
      }

      Object.assign(this.formParameters.data, data)
      Object.assign(this.formParameters.old_data, data)


      console.log(this.formParameters.data)
      this.loadingForm.next(false)

    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    this.loadingForm.next(true)
    let data = this.makeClone(this.formParameters.data)
    let old_data = this.makeClone(this.formParameters.old_data)

    console.log(this.formParameters.data)
    if (JSON.stringify(data) == JSON.stringify(old_data)){
      this.toastr.warning('No Changes Detected!')
      this.loadingForm.next(false)
      return;
    }

    console.log(data)

    this.ug_service.save(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.save) {
        this.toastr.success('User Group Changed!');
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
