import { Component, OnInit, Input } from '@angular/core';
//Toastr
import { ToastrService } from 'ngx-toastr';
//RXJS
import { BehaviorSubject } from 'rxjs';
//Service//
import { UserGroupService } from '../../../../../pages/gui/api-user-groups/user-group.service'

@Component({
  selector: 'kt-api-user-group-form',
  templateUrl: './api-user-group-form.component.html',
  styleUrls: ['./api-user-group-form.component.scss']
})
export class ApiUserGroupFormComponent implements OnInit {

  @Input() options

  @Input() validation
  @Input() loading
  rights = new BehaviorSubject([])

  constructor(
    private toastr:ToastrService,
    private service: UserGroupService
  ) { }

  ngOnInit() {
    this.service.rights().subscribe(data => {
      this.rights.next(data)
      console.log(data)
    })
    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
  }

  addRole(index: number){
    this.options.data.rights += Math.pow(2, index)
  }

  delRole(index: number){
    this.options.data.rights -= Math.pow(2, index)
  }

  roleChecker(index: number){
    let tempStr = this.options.data.rights.toString(2).split("").reverse()
    return tempStr[index] && tempStr[index] == 1
  }


}
